html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    background-color: white;
}

h1 {
    color:  #202A44;
    font-family: "Roboto", sans-serif;
    font-weight: 600;
}

li {
    color: #031019;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
}

.site-wrap {
    overflow-x: hidden;
    position: relative;
}

.accordion-container {
    width: 65%;
    min-width: 450px;
    max-width: 980px;
    margin: 40px auto;
}

.duties>ul>li {
    font-size: 18px;
    margin-top: 5px;
}

@media only screen and (max-width: 420px) {
    .accordion-container {
        width: 95%;
        min-width: 0px;
        margin: 40px auto;
    }
    h1 {
        font-size: 25px;
    }
    .duties>ul>li {
        font-size: 15px;
        margin-top: 5px;
    }
}
.typewrite, .typewrite-change {
    height: 160px;
    overflow: hidden;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-size: 60px;
    line-height: 60px;
    color:  #202A44;
    white-space: nowrap;
  }

.typewrite-change {
  white-space: nowrap;
}

@media only screen and (max-width: 420px) {
  .typewrite, .typewrite-change {
    font-size: 30px;
  }
}
.icon-bar {
    display: -webkit-flex;
    display: flex;
    margin-top: 10px;
    white-space: nowrap;
}


#canvas {
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 1;
}

.object-animation {
  position: absolute;
  margin-top: -55vh;
  margin-left: 30vw;
}

.typewrite-animation {
  position: absolute;
  margin-top: -75vh;
  margin-left: 25vw;
}

@media only screen and (max-width: 420px) {
  .typewrite-animation {
    margin-left: 4vw;
  }
  .object-animation {
    margin-left: 8vw;
  }
}

@media only screen and (max-width:1000px) and (min-width:420px) {
  .typewrite-animation {
    margin-left: 10vw;
  }
  .object-animation {
    margin-left: 15vw;
  }
}
.icon {
    color:  #202A44;
}

.social-button {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    outline: none;
    width: 40px;
    height: 40px;
    text-decoration: none;
}
.social-button__inner {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: calc(100% - 2px);
    height: calc(100% - 2px);
    border-radius: 100%;
    background: transparent;
    text-align: center;
}
.social-button i,
.social-button svg {
    position: relative;
    z-index: 1;
    transition: 0.3s;
}
.social-button i {
    font-size: 30px;
}
.social-button svg {
    height: 40%;
    width: 40%;
}
.social-button::after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    display: block;
    width: 0;
    height: 0;
    border-radius: 100%;
    transition: 0.15s;
}
.social-button:focus,
.social-button:hover {
    color: #fff;
}
.social-button:focus::after,
.social-button:hover::after {
    width: 100%;
    height: 100%;
    margin-left: -50%;
}

.notify {
    font-size: 20px;
}

.social-button--github::after {
    background: #6e5494;
}

.social-button--mail::after {
    background: #993b3b;
}

.social-button--linkedin::after {
    background: #0077b5;
}

.social-button--steam::after {
    background: black;
}

.social-button--instagram::after {
    background: radial-gradient(
        circle at 30% 107%,
        #fdf497 0%,
        #fdf497 5%,
        #fd5949 45%,
        #d6249f 60%,
        #285aeb 90%
    );
}

