    position: absolute;
    right: 20px;
    font-size: 16px;
}

.work-date, .work-title {
    color: lightgrey;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
}

.MuiAccordion-root {
    border-radius: 8px !important;
}


.MuiAccordionDetails-root {
